import React, { Component } from 'react'
import { Link } from 'react-router'
import styles from './header.css'
import axios from 'axios'

class header extends Component {

  constructor(props, context) {
    super(props, context)
    this.state = {
    	hint: false,
    	loading: false,
    	hasil: false,
    	gagal: false,
    	q: '',
    	list: [], 
    	jenis: 0
   	};
   	this.onKetik = this.onKetik.bind(this);
  }

  onOverlay() {
  	$(this).css('z-index','999');
	$('#gelapi').fadeIn(300);
	this.setState({
		hint: true,
		loading: false,
		hasil: false,
		gagal: false,
		keyword: '',
	});
	$('html, body').animate({
        scrollTop: $('.input-group-md').offset().top
    }, 600);
  }

  onCloseOverlay() {
  	this.setState({
		hint: false,
		loading: false,
		hasil: false,
		gagal: false
	});
	$('#gelapi').fadeOut(300, function(){
		$('#expose').css('z-index','1000');
	});
  }

  onKetik(event) {

  	this.setState({
		hint: false,
		loading: true,
		hasil: false,
		gagal: false,
		keyword: event.target.value
	});
  	
  	this.setState({q: event.target.value});

	if(!event.target.value.length) {
		
		this.setState({
			hint: true,
			loading: false,
			hasil: false,
			gagal: false
		});

	} else {

        axios.get('http://botek.ngekos.co.id/api/v1/cari/'+event.target.value)
        	.then((data) => {
        		if(data.data.length) {
        			this.setState({
	        			list: []
					});
	                var temp = this.state.list.concat(data.data);
	                this.setState({
	                    list: temp,
	                    loading: false,
	                    hasil: true
	                });
        		} else {
        			this.setState({
	                    loading: false,
	                    gagal: true
	                });
        		}
	        		
            });
	}

  }

  render() {
  	var hasil = this.state.list.map((item, index) => {
  		if(item.type_cari == 1) {
			var url = '/kota/' + item.slug;
		} else if(item.type_cari == 2) {
			var url = '/kecamatan/' + item.slug;
		} else if(item.type_cari == 3) {
			var url = '/kelurahan/' + item.slug;
		} else if(item.type_cari == 4) {
			var url = '/hunian/' + item.slug;
		}
        return (
        	<Link to={url} key={index}>
				<div className="pencarian-hasil-list">
					<div className="pencarian-hasil-judul">{item.label}</div>
					<div className="pencarian-hasil-subjudul">
						{function(){
							if(item.type_cari == 1) {
								return "Kabupaten / Kota";
							} else if(item.type_cari == 2) {
								return "Kecamatan"; 
							} else if(item.type_cari == 3) {
								return "Kelurahan"; 
							} else if(item.type_cari == 4) {
								return "Kost / Bangsalan / Kontrakan"; 
							}
						}.call(this)}
					</div>
				</div>
			</Link>
        );
    });

    return (
    	<div>
			<section className="ngekos-header-section">
	        	<div id="gelapi" onClick={this.onCloseOverlay.bind(this)} ></div>
				<aside className="ngekos-header-form">
					<div className="row">
						<div className="col-sm-6 col-sm-offset-3">
							<div className="input-group-md">
								<label className="fa fa-map-signs"></label>
								<input className="form-control input-lg" onClick={this.onOverlay.bind(this)} onInput={this.onKetik.bind(this)} placeholder="Pencarian Cepat.." required="required" name="q" type="text" autoComplete="off"/>
							</div>
						</div>
					</div>
				</aside>

				<div className="ngekos-header-wrapper">
					<img src="http://ngekos.co.id/img/slider-image-1.jpg" alt="" className="ngekos-header-img" />
					<div className="ngekos-header-judul">
						<h1 className="ngekos-header-title">Cari Mudah, Harga Murah</h1>
						<h4 className="ngekos-header-subtitle">Cari tau informasi kost-kostan, bangsalan dan kontrakan disekitarmu!</h4>
					</div>
				</div>

			</section>
			<div className="row">
				<div className="col-sm-10 col-sm-offset-1 pencarian-wrapper">

					<div className="pencarian-hint" style={{display: this.state.hint ? 'block' : 'none' }}>
			            <img src="data:image/png;base64,"/>
			            <div className="pencarian-hint-teks">
			                <span>Anda bisa mengetik <b>nama kota</b>, <b>kecamatan</b>, <b>kelurahan</b>, atau <b>nama kost </b>pada textbox diatas, kami akan membantu mencarikannya untuk anda.</span>
			            </div>
				    </div>
				
				    <div className="pencarian-loading-area" style={{display: this.state.loading ? 'block' : 'none' }}>
			            <img src="data:image/gif;base64,"/>
			            <div className="pencarian-loading-teks">Sedang mencari <b>"{this.state.keyword}"</b>, mohon tunggu sebentar ...</div>
			        </div>
				
					<div className="pencarian-gagal" style={{display: this.state.gagal ? 'block' : 'none' }}>
					    <img src="data:image/png;base64,"/>
					   	<div className="pencarian-gagal-teks">
					   		Kata kunci yang anda cari tidak ditemukan, coba cari kata kunci lain atau periksa kembali jaringan internet Anda
					   	</div>
					</div>
					<div className="pencarian-hasil" style={{display: this.state.hasil ? 'block' : 'none' }}>
						{hasil}
					</div>
			    
			    </div>
			</div>
		</div>
    )
  }

}

export default header


